/**
 * Logger  
 *    
 *
 * : 2025-11-13
 * :   + localStorage  + Loki ,   ,  
 */

export enum LogLevel {
  DEBUG = 'DEBUG',
  INFO = 'INFO',
  WARN = 'WARN',
  ERROR = 'ERROR',
}

export interface LogEntry {
  timestamp: number;
  level: LogLevel;
  module: string;
  message: string;
  data?: any;
  error?: Error;
}

export interface LokiConfig {
  host: string;
  labels: {
    app: string;
    service: string;
    environment: string;
    [key: string]: string;
  };
  batchSize?: number;
  flushInterval?: number;
}

export class Logger {
  private static logs: LogEntry[] = [];
  private static maxLogs: number = 1000; //    
  private static storageKey: string = 'roulette_multiplayer_logs';

  // Loki 
  private static lokiConfig: LokiConfig | null = null;
  private static lokiEnabled: boolean = false;
  private static lokiBatch: LogEntry[] = [];
  private static lokiFlushInterval: number | null = null;

  /**
   * DEBUG  
   * @param module  (: 'PeerManager', 'RoomManager')
   * @param message  
   * @param data   ()
   */
  static debug(module: string, message: string, data?: any): void {
    this.log(LogLevel.DEBUG, module, message, data);
  }

  /**
   * INFO  
   * @param module 
   * @param message  
   * @param data   ()
   */
  static info(module: string, message: string, data?: any): void {
    this.log(LogLevel.INFO, module, message, data);
  }

  /**
   * WARN  
   * @param module 
   * @param message  
   * @param data   ()
   */
  static warn(module: string, message: string, data?: any): void {
    this.log(LogLevel.WARN, module, message, data);
  }

  /**
   * ERROR  
   * @param module 
   * @param message  
   * @param error Error  ()
   */
  static error(module: string, message: string, error?: Error): void {
    this.log(LogLevel.ERROR, module, message, undefined, error);
  }

  /**
   *   ( )
   * @param level  
   * @param module 
   * @param message  
   * @param data  
   * @param error Error 
   */
  private static log(
    level: LogLevel,
    module: string,
    message: string,
    data?: any,
    error?: Error
  ): void {
    const entry: LogEntry = {
      timestamp: Date.now(),
      level,
      module,
      message,
      data,
      error: error ? {
        name: error.name,
        message: error.message,
        stack: error.stack,
      } as any : undefined,
    };

    //  
    this.logs.push(entry);

    //       
    if (this.logs.length > this.maxLogs) {
      this.logs = this.logs.slice(-this.maxLogs);
    }

    // localStorage  ( )
    this.saveToStorage();

    // Loki 
    this.sendToLoki(entry);

    //  
    this.printToConsole(entry);
  }

  /**
   * localStorage  
   */
  private static saveToStorage(): void {
    try {
      const recentLogs = this.logs.slice(-100); //  100 
      localStorage.setItem(this.storageKey, JSON.stringify(recentLogs));
    } catch (err) {
      // localStorage     
    }
  }

  /**
   *   
   * @param entry  
   */
  private static printToConsole(entry: LogEntry): void {
    const time = new Date(entry.timestamp).toLocaleTimeString('ko-KR');
    const prefix = `[${time}] [${entry.module}]`;

    switch (entry.level) {
      case LogLevel.DEBUG:
        console.debug(prefix, entry.message, entry.data || '');
        break;
      case LogLevel.INFO:
        console.log(prefix, entry.message, entry.data || '');
        break;
      case LogLevel.WARN:
        console.warn(prefix, entry.message, entry.data || '');
        break;
      case LogLevel.ERROR:
        console.error(prefix, entry.message, entry.error || entry.data || '');
        break;
    }
  }

  /**
   *    (JSON )
   * @returns JSON  
   */
  static exportLogs(): string {
    return JSON.stringify(this.logs, null, 2);
  }

  /**
   *  
   * @param filename  (: roulette-logs-{timestamp}.json)
   */
  static downloadLogs(filename?: string): void {
    const defaultFilename = `roulette-logs-${Date.now()}.json`;
    const blob = new Blob([this.exportLogs()], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = filename || defaultFilename;
    a.click();
    URL.revokeObjectURL(url);
  }

  /**
   *  
   */
  static clearLogs(): void {
    this.logs = [];
    try {
      localStorage.removeItem(this.storageKey);
    } catch (err) {
      // 
    }
    console.log('[Logger]  .');
  }

  /**
   * localStorage  
   */
  static loadFromStorage(): void {
    try {
      const stored = localStorage.getItem(this.storageKey);
      if (stored) {
        this.logs = JSON.parse(stored);
        console.log(`[Logger] ${this.logs.length}  .`);
      }
    } catch (err) {
      console.error('[Logger]   :', err);
    }
  }

  /**
   *    
   * @returns  
   */
  static getLogCount(): number {
    return this.logs.length;
  }

  /**
   * Loki  
   * @param config Loki 
   */
  static initLoki(config: LokiConfig): void {
    this.lokiConfig = {
      ...config,
      batchSize: config.batchSize || 10,
      flushInterval: config.flushInterval || 5000, // 5 
    };
    this.lokiEnabled = true;

    console.log('[Logger] Loki  :', config.host, config.labels);

    //   
    this.lokiFlushInterval = window.setInterval(() => {
      this.flushLokiBatch();
    }, this.lokiConfig.flushInterval);
  }

  /**
   * Loki  
   * @param entry  
   */
  private static sendToLoki(entry: LogEntry): void {
    if (!this.lokiEnabled || !this.lokiConfig) return;

    //  
    this.lokiBatch.push(entry);

    //      
    if (this.lokiBatch.length >= this.lokiConfig.batchSize!) {
      this.flushLokiBatch();
    }
  }

  /**
   * Loki  
   */
  private static flushLokiBatch(): void {
    if (!this.lokiEnabled || !this.lokiConfig || this.lokiBatch.length === 0) {
      return;
    }

    const batch = [...this.lokiBatch];
    this.lokiBatch = [];

    const streams = [
      {
        stream: this.lokiConfig.labels,
        values: batch.map(entry => [
          `${entry.timestamp}000000`, //   (timestamp * 1000000)
          JSON.stringify({
            level: entry.level,
            module: entry.module,
            message: entry.message,
            data: entry.data,
            error: entry.error ? {
              name: entry.error.name,
              message: entry.error.message,
              stack: entry.error.stack,
            } : undefined,
          }),
        ]),
      },
    ];

    // Loki Push API 
    fetch(`${this.lokiConfig.host}/loki/api/v1/push`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ streams }),
    })
      .then(response => {
        if (!response.ok) {
          console.error('[Logger] Loki  :', response.status, response.statusText);
        }
      })
      .catch(error => {
        console.error('[Logger] Loki  :', error);
      });
  }

  /**
   * Loki  
   */
  static disableLoki(): void {
    if (this.lokiFlushInterval !== null) {
      clearInterval(this.lokiFlushInterval);
      this.lokiFlushInterval = null;
    }

    //   
    this.flushLokiBatch();

    this.lokiEnabled = false;
    console.log('[Logger] Loki  ');
  }
}

//      
if (typeof window !== 'undefined') {
  Logger.loadFromStorage();

  // Loki  (https://logs.samlab.co.kr)
  Logger.initLoki({
    host: 'https://logs.samlab.co.kr',
    labels: {
      app: 'roulette-multiplayer',
      service: 'multiplayer-game',
      environment: 'production',
      hostname: typeof window !== 'undefined' ? window.location.hostname : 'unknown',
    },
    batchSize: 10,
    flushInterval: 5000,
  });
}
